              <mu-button style=" height:40px;width: 120px" color="primary"></mu-button>
              <mu-list style=" height:160px;width: 120px" slot="content">
                <mu-list-item style=" height:40px;width: 120px"  button  @click="menuAction()" >
                  <mu-list-item-title style=" height:40px;width: 80px"></mu-list-item-title>
                </mu-list-item>
                <mu-list-item style=" height:40px;" button @click="save()">
                  <mu-list-item-title style=" height:40px;"></mu-list-item-title>
                </mu-list-item>
                <mu-list-item style=" height:40px;" button @click = "newBlank()">
                  <mu-list-item-title style=" height:40px;"></mu-list-item-title>
