          <!--</div>-->
              </li>

          <li>
            <quill-editor style=" height:600px;" v-model="content"
                          ref="myQuillEditor"
                          :options="editorOption"
                          @blur="onEditorBlur($event)"
                          @focus="onEditorFocus($event)"
                          @ready="onEditorReady($event)"
                          @change="onEditorChange($event)"
            >
            </quill-editor>
          </li>
          </ul>

    <mu-drawer :open.sync="open" :docked="docked" :right="position === 'right'">
      <mu-list >


        <mu-list-item  @click="didSelected(index)" button  v-for="(role,index) in arrData " :key="index">
        <mu-list-item-title> {{role.title}} </mu-list-item-title>
        </mu-list-item>
        <!--<mu-list-item button>-->
          <!--<mu-list-item-title>Menu Item 2</mu-list-item-title>-->
        <!--</mu-list-item>-->
        <!--<mu-list-item  @click="open = false" button>-->
          <!--<mu-list-item-title>Close</mu-list-item-title>-->
        <!--</mu-list-item>-->
      </mu-list>
    </mu-drawer>

  </div>
</template>

<script>
  import 'material-design-icons';
  import 'quill/dist/quill.core.css'
  import 'quill/dist/quill.snow.css'
  import 'quill/dist/quill.bubble.css'
  import * as Quill from 'quill' //
  import { quillEditor } from 'vue-quill-editor'
  import { ImageDrop } from 'quill-image-drop-module';

  import {container, ImageExtend, QuillWatch} from 'quill-image-extend-module';
  import ImageResize from 'quill-image-resize-module'
  // import $ from 'jquery'
  Quill.register('modules/ImageExtend', ImageExtend);
  Quill.register('modules/imageDrop', ImageDrop);
  Quill.register('modules/ImageResize', ImageResize)

  export default {
  props: ['price', 'title', 'img'],
  data () {


    return {



      editor: null,   // 
      content: `<p></p><p><br></p><ol></ol>`, // 
      editorOption: { //  

        modules:{
          // placeholder: '',
          toolbar: {
            container: container,  // container
            handlers: {
              'image': function () {  // 
                QuillWatch.emit(this.quill.id)
              }
            }
          },
          ImageResize: {},
          imageDrop:false,
            ImageExtend: {  // {}  base64
              name: 'file',  // 
              size: 20,  //  M1M = 1024kb
              action: '/jinjiapi/lanbitouGetUploadQiniuFileUrl',  // , actionbase64
              // response 
              // {code: 200; data:{ url: 'baidu.com'}}
              //  return res.data.url
              response: (res) => {
                console.log(''+ res.data.url);
      // var imgReg = /<img.*?(?:>|\/>)/gi;
      // let arrImg =  imgReg.test(this.content);
      // let str = arrImg[arrImg.length-1];

//
//     var imgs = this.content.match(/<img[^>]+>/g);
//       console.log(imgs);
// if(imgs &&imgs.length >0){
//
//
//       this.content = this.content.replace(imgs[imgs.length-1],"<br/>");
//     console.log( this.content);}
    if(res.code===400){
      alert('');
    }else
    return res.data.url;




            },
            headers: (xhr) => {
                },  //  
          sizeError: () => {},  // 
        start: () => {console.log('start')},  //  
      end: () => {console.log('end')},  //  
      error: () => {console.log('error')},  //  
    success: () => {console.log('success')},  //   
    change: (xhr, formData) => {
      // xhr.setRequestHeader('myHeader','myValue')
      // formData.append('token', 'myToken')
    } //  headersformData


        },


        },
      },

      //
    timer: '',
      searchKey:'',
      code: '',
      mail:'630806244@qq.com',
      show:true,
      timer:null,
      count:'',
      docked: false,
      loading:null,
      open: false,
      position: 'left',
      nowIndex:-1,
      arrData: [
        { id: 1, title: '' },

 ],
    }
  },
    components: {
      quillEditor
    },
  // components: {
  //   HomeHeader,
  //   CommonFooter,
  //   List
  // },

  created () {
//jsctrl+s
    this.loading = this.$loading();

    if(this.mail===""){
      return;
    }
    let code = 0
    let code2 = 0
    let self = this;
    document.onkeydown = function (e) {
      let evn = e || event;
      let key = evn.keyCode || evn.which || evn.charCode;

      if (e.keyCode == 83 && (navigator.platform.match("Mac") ? e.metaKey : e.ctrlKey)){
        self.save();
        e.preventDefault();
    }

    };


      this.getData(this.mail);

    // const loading = this.$loading({
    //   // ...options
    // });
    // setTimeout(() => {
    //   loading.close();
    // }, 30);

    this.timer = setInterval( () => {

      self.save()

  }, 60*1000);




  },
    mounted() {
let self = this;
      this.timer = setInterval(  self.save(),6* 1000);
    },
    beforeDestroy() {
      clearInterval(this.timer);
    },
  //
  methods:{

    menuAction(){
      this.open=!this.open;
      console.log('menu')
    },
    getData(mail){
      this.$http.post('/jinjiapi/getBeiwangluTypeWeb', {userId: mail}).then(res => {
        res = res.body;
      console.log('getallbeiwanglu'+res);
console.log(res.data[0]);
this.loading.close();
      if (res.status) {
        this.arrData = res.data;

      } else {

      }

    });
    },
    getNewDate() {
    var date = new Date();
    console.log(date);
    var transverse = "-";
    var Verticalpoint = ":";
    var month = date.getMonth() + 1;//
    var strDate = date.getDate();//
    var strHour = date.getHours();//...
    var strMinute = date.getMinutes();//
    var strSeconde = date.getSeconds();//
    //   ... 1~9
    //0
    if (month >= 1 && month <= 9) {
      month = "0" + month;
    }
    console.log(month);
    if (strDate >= 1 && strDate <= 9) {
      strDate = "0" + strDate;
    }
    if (strHour >= 1 && strHour <=9) {
      strHour = "0" + strHour
    }
    console.log(strHour);
    if (strMinute >= 1 && strMinute <= 9) {
      strMinute = "0" + strMinute;
    }

    if (strSeconde >= 1 && strSeconde <= 9) {
      strSeconde = "0" + strSeconde;
    }
    //
    var NewDate = date.getFullYear() + '' + month + '' + strDate + " " +
      strHour + Verticalpoint + strMinute + Verticalpoint + strSeconde;
    //
    return NewDate;
  },
    insertData (){
      let data  = {html:this.content,

      text:"",
        userId:this.mail,
        memo_id: (new Date()).getTime(),
        picNum:0,
        contents:this.text,
        imageNames:'',
        textContent:this.text,
        headerIcon:'',
        memo_time:this.getNewDate(),
        updateTime:this.getNewDate(),
        title:'',
        members:this.mail,
        location:'()',
        voiceNum:0,
      };

      this.$http.post('/jinjiapi/inserBeiwanglu',data).then(res => {
        res = res.body;
      console.log(res);

      if (res.status) {
        this.$toast.success('');
        this.nowIndex = this.arrData.length;
        this.getData(this.mail);

      } else {
        this.$toast.success('');
      }

    });
    },
    clockInterval(){
      if(this.nowIndex!==-1){//
      this.save();
      }else {//
        // this.$toast.message('!');
      }

    },
    newBlank () {
      this.save();
      this.content = '';
      this.nowIndex = -1;
      this.text = '';
    },
    deleteThis () {

if(this.nowIndex === -1 ){
  this.$toast.error('');
  return;
}

console.log(this.arrData[this.nowIndex]);
      let data  = {id:this.arrData[this.nowIndex].id};
      this.$http.post('/jinjiapi/deleteBeiwangluByid', data).then(res => {
        res = res.body;
      console.log(res);

      if (res.status) {
        this.$toast.success('');
        this.getData(this.mail);
      } else {
        this.$toast.error('');
      }

    });

      this.arrData.splice(this.nowIndex,1);

      this.content = '';
      this.nowIndex = -1;
      this.text = '';

    },

    didSelected (index) { //test1
      if(this.nowIndex!==-1){
        this.arrData[this.nowIndex].html = this.content;
        this.arrData[this.nowIndex].contents = this.text;
        console.log(this.arrData);
        this.save();

      }
      this.open = false;
      this.nowIndex = index;
      console.log(index);
      // this.content = 'sasa'+index;
      this.content = this.arrData[index].html;
      this.text = this.arrData[index].html;
    },
    cancelSearchAction () {
      this.searchKey = '';
      this.getData(this.mail);
    },
    searchAction () {//
      if(this.searchKey===""){

        return;
      }

let tempArr = [];
for(let i = 0 ; i < this.arrData.length; i ++){
  if(this.arrData[i].html.indexOf(this.searchKey) >= 0){
    tempArr.push(this.arrData[i])
  }
}
      if(tempArr.length === 0 ){
        this.$toast.message('!');
        return;
      }else {
  this.open = true;
        this.arrData = tempArr;
      }



    },

    save () { //test1

      if(this.mail===""){
        return;
      }
      if(this.nowIndex!==-1){//
        this.arrData[this.nowIndex].html = this.content;
        let title = '';

          var r = /#.*?#/;
          var a =this.text.match(r);
          if(!a ){

            a =[this.text.slice(0,25)];
          }
          a[0]=a[0].replace("#","");
          if(!a)a =[''];
        this.arrData[this.nowIndex].title  = a[0];

        let data  = {html:this.content,


          userId:this.mail,
          memo_id:   this.arrData[this.nowIndex].memo_id,
          picNum : this.arrData[this.nowIndex].picNum,
          contents:  this.arrData[this.nowIndex].text,
          imageNames:  this.arrData[this.nowIndex].imageNames,
          textContent:  this.arrData[this.nowIndex].text,
          headerIcon:  this.arrData[this.nowIndex].headerIcon,
          memo_time:  this.arrData[this.nowIndex].memo_time,
          updateTime:this.getNewDate(),

          members:  this.arrData[this.nowIndex].members,
          location:  this.arrData[this.nowIndex].location,
          voiceNum:  this.arrData[this.nowIndex].voiceNum,
        };

        this.$http.post('/jinjiapi/updateBeiwanglu', data).then(res => {
          res = res.body;
        console.log(res);

        if (res.status) {
          this.$toast.success('');
          this.getData(this.mail);
        } else {
          this.$toast.error('');
        }

      });
        this.$toast.success('!');
      }else {//
        if(this.content && this.content !=="" &&this.content !== '<p></p><p><br></p><ol></ol>')
        this.insertData();
        // this.$toast.message('!');
      }

    },


    onEditorBlur(quill) {
      console.log('editor blur!', quill)
      if(this.nowIndex!==-1){
        // this.arrData[this.nowIndex].title = this.content;
        console.log(this.arrData);
      }
    },
    onEditorFocus(quill) {
      console.log('editor focus!', quill)
      console.log(this.content);
    },
    onEditorReady(quill) {
      console.log('editor ready!', quill)
    },
    onEditorChange({ quill, html, text }) {
      console.log('editor change!', quill, html, text)
      this.content = html;
      this.text = text;
      this.arrData[this.nowIndex].text = text;
      this.arrData[this.nowIndex].html = html;
    }



  },
    computed: {
      editor() {
        return this.$refs.myQuillEditor.quill
      }
    },
    mounted() {
      console.log('this is current quill instance object', this.editor)
    }
}
</script>

<style lang="css" scoped>
  .goods-list {
    margin-right:10px;

    margin-bottom:10px;
  },
  .ql-editor{
    /*height:500px;*/
  }


</style>



